.logo {
	width:250px;
	margin-top: 10px;
}

.fa-bars {
	display: none;
	font-size: 4rem;
	@include breakpoint(standard) {
		display:block;
	}
}

.fa-times {
	font-size: 4rem;
}

.fa-sign-in {
	color: #F1D644;
}

ul.menu {
	margin: 0;
	padding: 0;
	li {
		margin:0 20px;
		list-style:none;
		a {
			font-weight: 800;
			font-size: 14px;
			color: #464646;
			text-transform: uppercase;
		}
	}
}


ul.menu  {
	display: flex;
	align-items:center;
	@include breakpoint(standard) {
		display:none;
		flex-direction:column;
		position:absolute;
		width:300px;
		right:5%;
		top: 90px;
		background: white; 
		border:1px solid #ccc;
		border-radius:3px;
	}
	@include breakpoint(phone) {
		right:0;
		width:100%;
	}
	li.menu-open {
		border-bottom: 3px solid #8AC985;
	}
	> li.menu-item-has-children > a:after {
			content: ' \f078';
			font-family: FontAwesome;
	}
	> li.menu-item-has-children.menu-open > a:after {
			content: ' \f077';
			font-family: FontAwesome;
	}
	> li:last-of-type {
		border-bottom: none;
		&:hover {
			border-bottom:none;
		}
	}
	> li {
		position: relative;
		border-bottom: solid 3px white;
		transition:0.3s;
		font-family: 'Raleway', sans-serif;
		&:hover {
			border-bottom:3px solid #8AC985;
		}
		@include breakpoint(standard) {
			padding: 10px 0; 
			border-bottom:1px solid #ccc;
			// normal hover
			&:hover {
				border-left:2px solid #8AC985;
				border-bottom:1px solid #ccc;
			}
			// last menu item hover
			&:last-child {
				border-bottom: none;
				&:hover {
					border-left:2px solid #8AC985;
					border-bottom:none;
				}
			}
		}

		// SUBMENU
		> ul {
		padding-left: 0;
		position: absolute;
		top: 20px;
		right: -75px;
		left:0px;
		flex-direction: column;
		display: none;
		margin-top: 20px;
		background:  #FFF;
		box-shadow: 0 0 2px 1px lightgrey;
		@include breakpoint(standard) {
			position:static;
			box-shadow: 0 0 0;
			margin-top: 0;
		}
		> li {
			font-family: 'Assistant', sans-serif;
			border-bottom: 1px solid #E6E6E6;
			padding: 10px 0;
			margin: 0 10px;		
			&:last-child {
				border-bottom: none;
				}
			a {
				text-transform: initial!important;
					&:hover {
						color: #0b8c60;
					}	
				}
			}
		}
		li.menu-open:after {
			content: '\f077'!important;
			font-family: FontAwesome!important;
		}
	}
}


header .container {
	display:flex;
	justify-content: space-between;
	align-items: center;
	h1 {
		margin: 0;
	}
}

header {
	box-shadow: 0px 0 3px 3px lightgrey;
	position: fixed;
	top:0;
	left:0;
	right: 0;
	background: white;
	z-index: 3;
	a.login-button {
		font-family: 'Raleway', sans-serif;
		background: #27B489;
		color: #fff;
		font-weight: 800;
		font-size: 1.4rem;
		text-transform: uppercase;
		padding: 10px 25px;
		transition:0.3s;
		border-radius: 3px;
		@include breakpoint(standard) {
			display:block;
			border-radius: 0;
			padding: 14px 25px;
		}
		&:hover {
			background: #0B8C60;
		}
	}
}

li.locked, li.unlocked {
	display:flex;
	justify-content:space-between;
	align-items:center;
}


li.locked:after {
	content:'\f023';
	font-family: FontAwesome;
}

li.unlocked:after {
	content:'\f09c';
	font-family: FontAwesome;
}
